/*!
 * load styles
 */

// require('famous/core/famous.css');
require('./index.css');

/*!
 * module deps
 */

var Engine = require('famous/core/Engine'),
    RenderController = require('famous/views/RenderController'),
    Modifier = require('famous/core/Modifier'),
    ContainerSurface = require('famous/surfaces/ContainerSurface'),
    Page1 = require('page1'),
    Page2 = require('page2'),
    Page3 = require('page3'),
    Page4 = require('page4'),
    Page5 = require('page5'),
    Page6 = require('page6'),
    Page7 = require('page7'),
    Logo = require('logo');


/*!
 * register animation
 */

var Transitionable = require('famous/transitions/Transitionable'),
    SpringTransition = require('famous/transitions/SpringTransition'),
    WallTransition = require('famous/transitions/WallTransition'),
    SnapTransition = require('famous/transitions/SnapTransition');

Transitionable.registerMethod('spring', SpringTransition);
Transitionable.registerMethod('spring', SpringTransition);
Transitionable.registerMethod('wall', WallTransition);
Transitionable.registerMethod('snap', SnapTransition);

/*!
 * globals
 */

var mainContext = Engine.createContext(),
    renderController = new RenderController(),
    Pages = [Page1, Page2, Page3, Page4, Page5, Page6, Page7],
    surfaces = [];

/*!
 * kick off
 */

mainContext.setPerspective(500);


var container = new ContainerSurface({
  size: [800, 800]
});

container.add(new Modifier({
  align: [0.5, 0.5],
  origin: [0.5, 0.5]
}))
.add(new Logo());
surfaces.push(container);

Pages.forEach(function (Page, i) {
  var container = new ContainerSurface({
    size: [800, 800],
    properties: {
      backgroundColor: 'hsl(' + (i * 360 / 40) + ', 100%, 50%)'
    }
  });
  container.add(new Page());
  surfaces.push(container);
});


mainContext
  .add(new Modifier({
    align: [0.5, 0.5],
    origin: [0.5, 0.5],
  }))
  .add(renderController);

var counter = 0;
renderController.show(surfaces[counter]);
Engine.on('click', function() {
    var next = (counter++ + 1) % surfaces.length;
    this.show(surfaces[next]);
}.bind(renderController));



